import './..//App.css';
import Header from './Header';
import SearchForm from './SearchForm';
import TableData from './TableData';
import AddUser from './AddUser';
import React, { Component } from 'react';
import DataUser from './Data.json';
const uuidv1 = require('uuid/v1');
class App extends Component {
  ketNoi = () => { }
  constructor(props) {
    super(props);
    this.state = {
      hienThiForm: true,
      hienThiNut: true,
      data: [],
      searchText: '',
      hienThiEdit: false,
      doiTuongUser : {}
    }
  }
componentWillMount() {
 if(localStorage.getItem('userData') === null){
  localStorage.setItem('userData', JSON.stringify(DataUser));
 }
 else{
   var temp = JSON.parse(localStorage.getItem('userData'));
   this.setState({
     data: temp
   });
 }
}  
  deleteButtonClick = (idUser1) => {
    var tempData = this.state.data;
    tempData = tempData.filter((item) => {
      return item.id !== idUser1;
    })
    this.setState({
      data : tempData
    });
    localStorage.setItem('userData', JSON.stringify(tempData));
  }  
  layThongTinSua = (thongTinSua) => {   
    this.state.data.forEach((value,key) => {
      if(thongTinSua.id === value.id ){
       value.name = thongTinSua.name;
       value.tel = thongTinSua.tel;
       value.Permission = thongTinSua.Permission;
      }
    })
    localStorage.setItem('userData', JSON.stringify(this.state.data));
  } 
  thayDoiTrangThai = () => {
    this.setState({
      hienThiForm: !this.state.hienThiForm,
      hienThiNut: !this.state.hienThiNut
    })
  }
  thayDoiTrangThaiEdit = () => {
    this.setState({
      hienThiEdit: !this.state.hienThiEdit
    });
  }
  getTextSearch = (dl) => {
    this.setState({
      searchText: dl
    });
  }
  getNewUserData = (name, tel, Permission) => {
    var item = {};
    item.id = uuidv1();
    item.name = name;
    item.tel = tel;
    item.Permission = Permission;
    var items = this.state.data;
    items.push(item);
    this.setState({
      data: items
    });   
    localStorage.setItem('userData', JSON.stringify(items));
  }
  editFun = (user) => {
    console.log(user);
    this.setState({
      doiTuongUser  : user
    });
  }
  render() {
    var ketqua = [];
    this.state.data.forEach((item) => {
      if (item.name.indexOf(this.state.searchText) !== -1)
        ketqua.push(item);
    });
    console.log(ketqua);
    return ( 
      <div>
        <Header></Header>
        <div className="container">
          <div className="row">
            <div className="col-12">
              <SearchForm
                layThongTinSua = {(thongTinSua) => {this.layThongTinSua(thongTinSua)} }
                doiTuongUser = {this.state.doiTuongUser}
                thayDoiTrangThaiEdit={() => { this.thayDoiTrangThaiEdit() }}
                hienThiEdit = {this.state.hienThiEdit}
                getTextSearch={(dl) => this.getTextSearch(dl)}
                hienThiNut={this.state.hienThiNut} ketNoi={() => this.ketNoi()} thayDoiTrangThai={() => this.thayDoiTrangThai()}></SearchForm>
            </div>
            <TableData
              deleteButtonClick1 = {(idUser) => {this.deleteButtonClick(idUser)} }
              layThongTinSua = {(thongTinSua) => {this.thongTinSua(thongTinSua)} }
               thayDoiTrangThaiEdit={() => { this.thayDoiTrangThaiEdit() }}
              dataUserProps={ketqua} editFun={(user) => { this.editFun(user) }}></TableData>
            <AddUser hienThiForm={this.state.hienThiForm}
              getNewUserData={(name, tel, Permission) => { this.getNewUserData(name, tel, Permission) }}
            >
            </AddUser>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
