import React, { Component } from 'react';
class TableDataRow extends Component {
    permissionShow = () => {
        if (this.props.Permission === 1)
            return "Admin";
        else if (this.props.Permission === 2)
            return "Morator";
        else
            return "Normal User";
    }
    editClick = () => {
        this.props.thayDoiTrangThaiEdit();
        this.props.editFunClick();
    } 
    render() {
        return (

            <tr>
                <td scope="row">{this.props.stt + 1}</td>
                <td>{this.props.name} </td>
                <td>{this.props.tel}</td>
                <td>{this.permissionShow()}</td>
                <td>
                    <div 
                    className="btn btn-warning sua " 
                    onClick={() => { this.editClick()}}
                   
                    >
                        <i className="fa fa-edit    "  />
                        Sửa
          </div>
                    <div className="btn btn-danger xoa" onClick = {(idUser) => {this.props.deleteButtonClick2(this.props.id)} } >
                        <i className="fa fa-delete" />
                        Xóa
          </div>
                </td>
            </tr>
        );
    }
}
export default TableDataRow;