import React, { Component } from 'react';
import TableDataRow from './TableDataRow';

class TableData extends Component {

    mappingDataUser = () => this.props.dataUserProps.map((value,key) => (
        <TableDataRow 
        deleteButtonClick2 = {(idUser) => {this.props.deleteButtonClick1(idUser)} }
        thayDoiTrangThaiEdit={() => { this.props.thayDoiTrangThaiEdit() }}
        editFunClick = {(user) => {this.props.editFun(value)} }
        key={key} stt ={key} id={value.id} name={value.name} tel={value.tel} Permission = {value.Permission}/>
    ))
    render() {
        
        return (
            <div className="col">

                <table className="table table-striped  table-hover">
                    <thead className="thead-inverse">
                        <tr>
                            <th >STT</th>
                            <th>TÊN</th>
                            <th>ĐIỆN THOẠI</th>
                            <th>QUYỀN</th>
                            <th>THAO TÁC</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.mappingDataUser()}        
                    </tbody>
                </table>
            </div>


        );
    }
}

export default TableData;