import React, { Component } from 'react';

class AddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      trangThaiChinhSua: false,
      id: "",
      name: "",
      tel: "",
      Permission: ""
    }
  }
  isChange = (event) => {
    const value = event.target.value;
    const name = event.target.name;
   this.setState({
     [name]: value,
   });
   var item = {};
   item.id = this.state.id;
   item.name = this.state.name;
   item.tel = this.state.tel;
   item.Permission = this.state.Permission;
   console.log(item); 
  } 
  kiemTraTrangThai = () =>{
    
    if(this.props.hienThiForm == true){
      
      return (
        <div className="col-12">
      <div>
      <form>
        <div className="form-group">
       <input 
       name = "name"
       onChange = {(event) => {this.isChange(event)}}
       type="text"  id className="form-control" placeholder="Tên User" aria-describedby="helpId" />
     </div>
 
       <div className="form-group ">
         <input 
         name =  "tel"
        onChange = {(event) => {this.isChange(event)}}
         type="text"  id className="form-control" placeholder="Điện thoại: " aria-describedby="helpId" />
       </div>
       <div className="form-group">
         <label htmlFor />
         <select 
         onChange = {(event) => {this.isChange(event)}}
         className="custom-select" name = "Permission" id required>  
           <option value>Chọn quyền </option>
           <option value={1}>Admin</option>
           <option value={2}>Modrator</option>
           <option value={3}>Normal</option>
           <option />
         </select>
       </div>
       <div className="form-group">
         <input className="btn btn-block btn-primary" 
         onClick = {() => {this.props.removeSpace()} }
         onClick = {(name,tel,Permission) => {this.props.getNewUserData(this.state.name,this.state.tel,this.state.Permission)}} 
         value ="Thêm mới" 
         type = "reset"/>
       </div>
       </form>
     </div>
 
     </div>)
    }
  }
  thayDoiTrangThai = () => {
    this.setState(
      {
        trangThaiChinhSua: !this.state.trangThaiChinhSua
      }
    );
    console.log("da thay doi");
  }
  hienThiNut = () => {
    if (this.state.trangThaiChinhSua === true) {
      return <div className="btn btn-block btn-outline-info pb-2 " onClick={() => this.thayDoiTrangThai()}>Thêm mới</div>
    }
    else
      return this.newMethod()
  }
  hienThiForm = () => {
    
    if (this.state.trangThaiChinhSua === true) {
      return (<div>
         <div className="form-group">
        <input type="text" name id className="form-control" placeholder="Tên User" aria-describedby="helpId" />
      </div>
        <div className="form-group">
          <input type="text" name id className="form-control" placeholder="Điện thoại: " aria-describedby="helpId" />
        </div>
        <div className="form-group">
          <label htmlFor />
          <select className="custom-select" name id required>
            <option value>Chọn quyền </option>
            <option value={1}>Admin</option>
            <option value={2}>Modrator</option>
            <option value={3}>Normal</option>
            <option />
          </select>
        </div>
        <div className="form-group">
          <div className="btn btn-block btn-primary">THÊM MỚI</div>
        </div>
      </div>)    
    }
  }
  newMethod() {
    return <div className="btn btn-block btn-outline-info" onClick={() => this.thayDoiTrangThai()}> Đóng lại </div>;
  }
  render() {
    return (
     
        <div className="card border-primary ">
            {this.hienThiNut()}
          {this.kiemTraTrangThai()}
        </div>
    
    );
  }
}

export default AddUser;