import React, { Component } from 'react';
class EditUser extends Component {
    constructor(props) {
        super(props);
        this.state = {
           id:this.props.doiTuongUser.id,
           name: this.props.doiTuongUser.name,
           tel: this.props.doiTuongUser.tel,
           Permission: this.props.doiTuongUser.Permission
        }
    }
    
    isChange = (event) => {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({
            [name] : value
        });
        
    } 
    saveButton = () => {
        var thongTinSua = {};
        thongTinSua.id = this.state.id;
        thongTinSua.name = this.state.name;
        thongTinSua.tel = this.state.tel;
        thongTinSua.Permission = this.state.Permission;
        this.props.thayDoiTrangThaiEdit();
        this.props.layThongTinSua(thongTinSua);
   
    } 
    render() {
        return (
            <div >
                <div>
                    <form>
                        <div className="form-group">
                            <input
                                onChange = {(event) => {this.isChange(event)} }
                                defaultValue={this.props.doiTuongUser.name}
                                name="name"
                                type="text" id className="form-control" placeholder="Tên User" aria-describedby="helpId" />
                        </div>
                        <div className="form-group ">
                            <input
                                onChange = {(event) => {this.isChange(event)} }
                                defaultValue={this.props.doiTuongUser.tel}
                                name="tel"
                                type="text" id className="form-control" placeholder="Điện thoại: " aria-describedby="helpId" />
                        </div>
                        <div className="form-group">
                            <label htmlFor />
                            <select
                                onChange = {(event) => {this.isChange(event)} }
                                defaultValue={this.props.doiTuongUser.Permission}
                                className="custom-select" name="Permission" id required>
                                <option value>Chọn quyền </option>
                                <option value={1}>Admin</option>
                                <option value={2}>Modrator</option>
                                <option value={3}>Normal</option>
                                <option />
                            </select>
                        </div>
                        <div className="form-group">
                            <input
                                onClick={() => this.saveButton()}
                                className="btn btn-block btn-danger"
                                value="Lưu thông tin "
                                type="button"
                            />
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default EditUser;