import React, { Component } from 'react';
import EditUser from './EditUser';
//props.layThongTinSua
class SearchForm extends Component {
    constructor(props) {
        super(props);
        this.state= {
            tempvalue : '',
            ttSua: {}
        }
    }
    isChange = (event) => {
        console.log(event.target.value);
        this.setState({
            tempvalue: event.target.value
        });
        this.props.getTextSearch(event.target.value);
    }
    hienThiEditUser = () => {
        if(this.props.hienThiEdit === true){
            return (
                <EditUser
                layThongTinSua = {(thongTinSua) => {this.props.layThongTinSua(thongTinSua)} }
                doiTuongUser = {this.props.doiTuongUser }
                 thayDoiTrangThaiEdit = {() => {this.props.thayDoiTrangThaiEdit()} }></EditUser>
            );
        }
    } 
    hienThiNut = () =>{
        if(this.props.hienThiNut === true){
            return(
                <div className="btn btn-block btn-outline-info pb-2" onClick = {() => this.props.ketNoi()} onClick = {() => this.props.thayDoiTrangThai()}>Thêm mới</div>
            )
        }
        else 
            return ( <div className="btn btn-block btn-outline-info" onClick = {() => this.props.ketNoi()} onClick = {() => this.props.thayDoiTrangThai()}  > Đóng lại </div>);
    }
    render() {
        return (
            <div>
                <div className="col-12">
                    {this.hienThiEditUser()}
                    <div className="form-group">
                        <div className="btn btn-group">
                            <input 
                            onChange = {(event) => this.isChange(event)} type="text" className="form-control" name id aria-describedby="helpId" placeholder="Nhập từ khóa" />
                            <button className="btn btn-info" onClick = {(dl) =>this.props.getTextSearch(this.state.tempvalue)}   >Tìm</button>
                        </div>
                    </div>
                </div>
                <div>
                 {this.hienThiNut()}
                </div>
            </div>
        );
    }
}

export default SearchForm;